:root {
  --lite-blue: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo {
  height: 100%;
}

.logo__img {
  width: 40px;
  height: 40px;
  padding-top: 10px;
}

.nav {
  align-items: center;
  height: 100%;
}

.nav_list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  align-items: center;
  margin-left: 20px;
  display: inline-block;
}

.nav_link {
  color: #000;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav_link:hover {
  color: var(--lite-blue);
}

.is-active {
  color: var(--lite-blue);
  position: relative;
}

.is-active:after {
  background-color: var(--lite-blue);
  content: " ";
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

